<template>
    <div class="collapse-trigger" role="button" @click="accordion($event)">
        <div class="card-header">
            <div class="card-header-title">
                <h2>{{ episode.title }}</h2>
                <a :href="episode.podcast" target="_blank" title="새창 열림"  v-if="episode.podcast" class="podcast">
                    <span class="icon is-medium"><i class="mdi mdi-podcast"></i></span>
                </a>
            </div>
            <div class="card-header-icon">
                <span class="icon">
                    <i class="mdi mdi-menu-down mdi-24px"></i>
                </span>
            </div>
        </div>
    </div>
    <div class="collapse-content"  style="display:none">
        <div class="card-content">
            <div class="content">
                <div class="main-link" v-if="episode.link">
                    <p>작품링크</p>
                    <ul v-if="!(episode.link == '상영중' || episode.link == '방영중')">
                        <li v-for="(link, i) in episode.link" :key="i"><a :href="link" target="_blank" title="새창 열림">{{ link }}</a></li>
                    </ul>
                    <p v-if="episode.link == '상영중' || episode.link == '방영중'">{{ episode.link }}</p>
                </div>
                <h3 v-if="episode.relatedworks">오늘의 자매들</h3>
                <ul class="related" v-if="episode.relatedworks">
                    <li v-for="(work, i) in episode.relatedworks" :key="i">
                        <h4>{{ work.title }}</h4>
                        <p><a :href="work.link" target="_blank" title="새창 열림">{{ work.link }}</a></p>
                    </li>
                </ul>
                <h3 v-if="episode.recommendation">추천작</h3>
                <ul class="related" v-if="episode.recommendation">
                    <li v-for="(work, i) in episode.recommendation" :key="i">
                        <h4>{{ work.title }}</h4>
                        <p><a :href="work.link" target="_blank" title="새창 열림">{{ work.link }}</a></p>
                    </li>
                </ul>
                <ul class="keywords-list" v-if="episode.keywords">
                    <li v-for="(keyword, i) in episode.keywords" :key="i" role="button" @click="getKeyword($event)">{{ keyword }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'episode-item',
    props: {
       episode : Object,
    },
    methods: {
        accordion(event) {
            let el = event.currentTarget;
            let target = el.nextElementSibling;
            if(target.style.display == 'none') {
                target.style.display = 'block';
                el.querySelector('.mdi-menu-down').classList.replace('mdi-menu-down', 'mdi-menu-up');
            }else {
                target.style.display = 'none'
                el.querySelector('.mdi-menu-up').classList.replace('mdi-menu-up', 'mdi-menu-down');
            }
        }, 
    }
}
</script>