import { createWebHistory, createRouter } from "vue-router";
import Main from "./../components/Main";
import SisterHood from "./../components/SisterHood/SisterHood";
import Yes24 from "./../components/Yes24/Yes24";
// import LiveSmarter from "./../components/LiveSmarter/LiveSmarter";
// import Americano from "./../components/Americano/Americano";
import Network from "./../components/Network/Network";

const routes = [
  {
    path: "/",
    component: Main,
  },
  {
    path: "/sisterhood",
    component: SisterHood,
  },
  {
    path: "/yes24",
    component: Yes24,
  },
  // {
  //   path: "/livesmarter",
  //   component: LiveSmarter,
  // },
  // {
  //   path: "/americano",
  //   component: Americano,
  // },
  {
    path: "/network",
    component: Network,
  }
  
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 